.portfolioContent {
	margin-left: 20px;
}
.portfolioByYearContent {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.portfolioItem {
	-ms-flex-pack: center;
	    justify-content: center;
	width: 204px;
	margin-right: 40px;
	margin-bottom: 20px;
	cursor: pointer;
}
.portfolioImg {
	width: 200px;
	border-color: white;
	border-style: solid;
	border-width: 2px;
}
.portfolioTitle {
	text-align: center;
}

.portfolioItemOuter {
	position: fixed;
	background-color: #353a3d;
	-ms-flex-item-align: end;
	    align-self: flex-end;
	height: 100vh;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-name: slide;
          animation-name: slide;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
	overflow-y: scroll;
}
.backIcon {
	width: 50px;
	margin-top: 20px;
	margin-left: 10px;
}
.portfolioLoad {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}
.portfolioItemContent {
	margin-left: 20px;
	margin-right: 20px;
}
.portfolioItemTitle {
	text-align: center;
	margin-top: 0px;
}
.portfolioItemImage {
	max-height: 70vh;
	max-width: 100%;
}
.portfolioItemImages {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-top: 30px;
}
.portfolioSpacer {
	margin-top: 50px;
}
.portfolioItemVideo {
	margin-top: 50px;
	width: 100%;
	height: 60vh;
}

@media only screen and (max-width: 640px) {
	@-webkit-keyframes slide {
	  from { left: 100%; }
	  to { left: 5%; }
	}
	@keyframes slide {
	  from { left: 100%; }
	  to { left: 5%; }
	}
}
@media only screen and (min-width: 640px) {
	@-webkit-keyframes slide {
	  from { left: 100%; }
	  to { left: 40%; }
	}
	@keyframes slide {
	  from { left: 100%; }
	  to { left: 40%; }
	}
}


